import tabs from '../../components/my-components/Tabs'
import vipBaseInfo from '../components/Vip-BaseInfo.vue'
import vipBussiness from '../components/Vip-Business.vue'
import vipCommission from '../components/Vip-CommissionInfo.vue'
import vipWin from '../components/Vip-Detail-Window.vue'
import getCommissionInfo from '../components/getCommisssionInfo.js'
let DataFrom = {};
let DataGo = {};
import {
    getJurisdiction
} from '../../components/my-components/getJurisdiction.js'
export default {
    components: {
        tabs,
        vipBaseInfo,
        vipBussiness,
        vipCommission,
        vipWin
    },
    data() {
        return {
            Jurisdiction: {},
            baseInfo: { //基本资料
                info1: {
                    registerTime: '',
                    origin: '',
                    vipClass: '',
                    vipTag: ''
                },
                info2: {
                    score: 0,
                    money: 0,
                    coupon: 0
                }
            },
            bussinessInfo: {
                info1: {
                    pay: 0,
                    bill: 0,
                    rights: 0,
                    refund: 0
                },
                info2: {
                    newBillTime: '',
                    newBrowserTime: ''
                }
            },
            vipInfo: {
                img: '',
                name: '',
                tel: '',
                verification: ''
            },
            tabIndex: 0,
            categories: [{
                    cateid: 0,
                    name: "基本资料"
                },
                {
                    cateid: 1,
                    name: "交易统计"
                },
                {
                    cateid: 2,
                    name: "分销商信息"
                }
            ],
            commissionInfo: { //分销商信息
                isCommission: 0, //是否分销商，0不是，1曾是，2是
                registerTime: '', //注册时间
                superDistributor: { //上级分销商
                    name: '总店'
                },
                beDistributorTime: { //成为分销商时间
                    value: ''
                },
                distributorClass: { //分销商等级
                    name: '普通分销商'
                },
                accumCommission: { //累计佣金
                    value: 0
                },
                hadGet: { //已提现佣金
                    value: 0
                },
                waitMoney: { //待入账佣金
                    value: 0
                },
                commBill: { //分销订单
                    value: 0
                },
                subDistributor: { //分销下级
                    num: 0
                },
                firstSub: { //一级
                    num: 0,
                    distributor: 0
                },
                secondSub: { //二级
                    num: 0,
                    distributor: 0
                },
                thirdSub: { //三级
                    num: 0,
                    distributor: 0
                }
            }
        }
    },
    methods: {
        checkBill() {
            this.Cacher.setData('vipDetail', {
                from: 'vipDetail',
                member_id: DataFrom.detail.info.id
            })
            uni.navigateTo({
                url: './vipBills?from=vipDetail'
            })
        },
        initPage() {
            this.Request('vipDetail', { //会员信息
                id: DataFrom.detail.info.id
            }).then(res => {
                let data = res.data;
                let commission = data.commission || {};
                this.baseInfo = { //基本资料
                    info1: {
                        registerTime: data.create_time,
                        origin: data.come_from,
                        vipClass: data.level_name,
                        vipTag: data.tags.map(item => item.name).join(';') || ' '
                    },
                    info2: {
                        score: data.credit,
                        money: data.balance,
                        coupon: data.coupon_count
                    }
                };
                this.vipInfo = {
                    img: data.avatar||'https://ceshiuser.100cms.com/static/dist/shop/image/noface.png',
                    name: data.nickname,
                    tel: data.mobile,
                    verification: data.mobile_verified == 0 ? '未验证' : '已验证',
                }
                this.commissionInfo = getCommissionInfo(commission);
            });
            this.Request('vipTradeInfo', { //会员交易信息 
                member_id: DataFrom.detail.info.id
            }).then(res => {
                let data = res.statistics;
                this.bussinessInfo = {
                    info1: {
                        pay: data.order_money,
                        bill: data.order_count,
                        rights: data.refund_count,
                        refund: data.refund_money
                    },
                    info2: {
                        newBillTime: res.last_order_time,
                        newBrowserTime: res.last_view_time
                    }
                }
            })
        },
        clickTab(val) {
            this.tabIndex = val.index;
        },
        clickBaseInfo(val) { //点击基础信息中的组件
            if (this.Jurisdiction.member_list_manage) {
                if (val.label == '余额' || val.label == '积分') {
                    if (val.type == 'add' || val.type == 'minus') { //增加或扣除
                        this.Cacher.setData('vipDetail', {
                            from: 'vipDetail',
                            value: val,
                            info: DataFrom.detail.info
                        });
                        uni.navigateTo({
                            url: './addSurplus?from=vipDetail'
                        })
                    } else if (val.type == 'clear') { //清空操作
                        this.pageLoading();
                        let api = '';
                        if (val.label == '余额') {
                            api = 'changeVipMoney'
                        } else if (val.label == '积分') {
                            api = 'changeVipScore'
                        }
                        let num = 0;
                        num = Math.min(this.money, this.curnum) * -1;
                        this.Request(api, {
                            member_id: DataFrom.detail.info.id,
                            sum: val.value * -1, //充值数量 正数添加余额, 负数减少积分
                            remark: '用户确认清空账户' //
                        }).then(res => {
                            this.closePageLoading();
                            this.initPage();
                        });
                    }
                } else if (val.label == '优惠券') {
                    this.Cacher.setData('vipDetail', {
                        from: 'vipDetail',
                        value: val,
                        info: DataFrom.detail.info
                    });
                    uni.navigateTo({
                        url: './coupon?from=vipDetail'
                    })
                }
            }else{
                this.Toast('暂无会员管理权限')
            }
        },
        clickDistriInfo(val) { //点击分销商信息中的组件 
            if (val.type == 'remove') { //取消分销商
                this.Request('changeCommissionStatus', {
                    member_id: DataFrom.detail.info.id,
                    status: 0
                }).then(res => {
                    this.initPage();
                })
            } else if (val.type == 'set') { //设为分销商
                this.Request('manualCommissionStatus', {
                    member_id: DataFrom.detail.info.id,
                    // status: 1
                }).then(res => {
                    this.initPage();
                })
                this.Request('changeCommissionStatus', {
                    member_id: DataFrom.detail.info.id,
                    status: 1
                }).then(res => {
                    this.initPage();
                })
            } else if (val.type == 'refuse') { //拒绝
                this.Request('changeCommissionStatus', {
                    member_id: DataFrom.detail.info.id,
                    status: -1
                }).then(res => {
                    this.initPage();
                })
            } else if (val.type == 'allow') { //通过审核
                this.Request('changeCommissionStatus', {
                    member_id: DataFrom.detail.info.id,
                    status: 1
                }).then(res => {
                    this.initPage();
                })
            }
        }
    },
    onShow() {
        this.initPage()
    },
    onLoad(option) {
        DataFrom = this.Cacher.getData(option.from);
        getJurisdiction.call(this).then(res => {
            this.Jurisdiction = res;
        }).catch(res => {
            this.Toast(res.message)
        })
    }
}
